
import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {Box, Sphere, Cylinder, Plane, Sky, Text, Scene} from 'react-aframe-ar'
import Marker from './components/Marker.js'
import AFrameRenderer from './components/AFrameRenderer.js'
import { Entity } from "aframe-react"
import Hammer from 'hammerjs'
import Back from './back.png'
import PreviewScene from './Scene.js'
import Circle from './circle.png'
import Square from './square.png'
import Triangle from './triangle.png'
import BackgroundImage from './style.png'

class App extends Component {
  constructor() {
    super()
    this.state = {
      startX: 0,
      startZ: 0,
      startY: 0,
      objX: 0,
      objY: 0,
      objZ: 0,
      startScale: 0.4,
      scale: 0.7,
      shape: 'pyramid',
      isCamerOpen: false,
      previewScene: 'pyramid' 
    }
  }
  componentDidMount() {
    // const app = document.querySelector('a-scene')
    // this.hammer = new Hammer(app)
    // this.hammer.get('pinch').set({ enable: true });
    // this.hammer.get('rotate').set({ enable: true });
    // this.hammer.get('pan').set({ direction: Hammer.DIRECTION_ALL });

    // this.hammer.on('panstart', this.handlePan);

    // this.hammer.on('panmove', this.handlePan);

    // this.hammer.on('pinchstart', this.handlePinch);

    // this.hammer.on('pinch', this.handlePinch);

    // this.hammer.on('rotatestart', this.handleRotate);

    // this.hammer.on('rotatemove', this.handleRotate);
  }
  initHammer() {
    const app = document.querySelector('#app')
    this.hammer = new Hammer(app)
    this.hammer.get('pinch').set({ enable: true });
    this.hammer.get('rotate').set({ enable: true });
    this.hammer.get('pan').set({ direction: Hammer.DIRECTION_ALL });

    this.hammer.on('panstart', this.handlePan);

    this.hammer.on('panmove', this.handlePan);

    this.hammer.on('pinchstart', this.handlePinch);

    this.hammer.on('pinch', this.handlePinch);

    this.hammer.on('rotatestart', this.handleRotate);

    this.hammer.on('rotatemove', this.handleRotate);
  }
  handlePan = e => {
    const { objX, objY, objZ, startX, startY, startZ } = this.state
    if ( e.type === 'panstart') {
      // console.log('start')
      this.setState({
        startX: objX,
        startY: objY,
        startZ: objZ
      })
    }
    this.setState({
      objX: this.state.startX + e.deltaX/150,
      // objZ: startZ + e.deltaZ/200,
      objY: this.state.startY + e.deltaY/150,
    })
    // console.log('handlepan: ', e.deltaX)
  }
  handlePinch = e => {
    const {scale} = this.state
    if ( e.type === 'pinchstart' ) {
      this.setState({
        startScale: scale
      })
    }
    this.setState({
      scale: this.state.startScale*e.scale
    })
    // console.log('handlepinch')
  }
  handleRotate = () => {
    // console.log('handleRotate')
  }
  selectShape = (shape) => {
    this.setState({
      shape: shape,
      isCamerOpen: true
    })
    this.initHammer()
  }
  hoverPreview = (shape) => {
    this.setState({
      previewScene: shape
    })
    console.log('hover shape: ', shape)
  }
  initState = () => {
    const video = document.querySelector('video')
    const body = document.querySelector('body')
    body.removeChild(video)
    this.hammer = null
    this.setState({
      startX: 0,
      startZ: 0,
      startY: 0,
      objX: 0,
      objY: 0,
      objZ: 0,
      startScale: 0.4,
      scale: 0.7,
      shape: 'pyramid',
      isCamerOpen: false,
      hoverShape: 'pyrimad'
    })
  }
  render() {
    const { objX, objY, objZ, startX, startY, startZ, scale, shape, isCamerOpen, previewScene } = this.state
    console.log('change: ', this.state.previewScene)
    let shapeDOM = null
    switch(shape) {
      case 'pyramid':
        shapeDOM = (
          // <a-cone 
          //   color="tomato" 
          //   radius-bottom="0.7" 
          //   radius-top="0.3"
          //   position={`${objX} ${objZ} ${objY}`}
          //   scale={`${scale} ${scale} ${scale}`}
          // >
          // </a-cone>
          // <a-cylinder open-ended="true" color="cyan" segments-radial="4"></a-cylinder>
          // <a-entity 
          //   color="#00798E"
          //   material='opacity: .95;'
          //   wireframe="true"
          //   position={`${objX} ${objZ} ${objY}`}
          //   scale={`${scale} ${scale} ${scale}`}
          //   geometry="primitive: cone; radiusBottom: 1; segmentsRadial: 4; radiusTop: 0">
          //   <a-animation attribute="rotation" to="0 360 0" dur="10000" easing="linear" repeat="indefinite" /> 
          // </a-entity>

          <a-cone 
            color="#00798E"
            material='opacity: .95;'
            wireframe="true"
            position={`${objX} ${objZ} ${objY}`}
            scale={`${scale} ${scale} ${scale}`}
            radius-bottom="1" segments-radial="4" radius-top="0">
             <a-animation attribute="rotation" to="0 360 0" dur="10000" easing="linear" repeat="indefinite" /> 
          </a-cone>
        )
        break
      case 'sphere':
        shapeDOM = (
          <a-sphere 
              color="#00798E" 
              radius="0.5"
              wireframe="true"
              material='opacity: .95;'
              position={`${objX} ${objZ} ${objY}`}
              scale={`${scale} ${scale} ${scale}`}>
              <a-animation attribute="rotation" to="0 360 0" dur="10000" easing="linear" repeat="indefinite" /> 
          </a-sphere>
        )
        break
      case 'box':
        shapeDOM = (
          <a-box 
              color='#00798E' 
              material='opacity: .95;'
              position={`${objX} ${objZ} ${objY}`} 
              scale={`${scale} ${scale} ${scale}`}>
              <a-animation attribute="rotation" to="0 360 0" dur="10000" easing="linear" repeat="indefinite" /> 
          </a-box> 
        )
        break
      default:
        break
    }
    const PreviewDOM = (
      <PreviewScene 
          geometry={previewScene}
        />
    )
    return (
      <div 
        className={isCamerOpen ? "App open" : "App"}
        id="app"
      >
        {!isCamerOpen && <img id="bg-img" src={BackgroundImage} />}
        {isCamerOpen && <AFrameRenderer
          inherent={true}
          arToolKit={{
            debugUIEnabled: false
          }}
          >
          <Marker parameters={{ preset: 'hiro' }}>
            {shapeDOM}
            <a-text
                className="text-info"
                position={`${objX} ${objZ+0.5} ${objY}`} 
                text={`value: ${shape}; font-size: 50px; color: black; text-align: center`} wrap-count="100"></a-text>
            {/* <a-cylinder position={`${objX} ${objZ} ${objY}`} color="crimson" height="1" radius="0.15"></a-cylinder> */}
          </Marker>
        </AFrameRenderer>}
        {isCamerOpen && (
          <div 
            className="home-icon"
            onClick={()=>this.initState()}
          >
              <img src={Back}/>
          </div>)
        }
        {!isCamerOpen && <ShapeList
          clickFunction={this.selectShape}
          hoverPreview={this.hoverPreview} 
        />}
        {!isCamerOpen && PreviewDOM}
      </div>
    );
  }
}

export default App;


const ShapeList = ({clickFunction, hoverPreview}) => {
  const shapes = [
    {
      shape: 'pyramid',
      imageUrl: Triangle
    },
    {
      shape: 'sphere',
      imageUrl: Circle
    },
    {
      shape: 'box',
      imageUrl: Square
    }
  ]
  const shapeDoms = shapes.map( e => {
    return (
      <div 
        className="shape-info"
        onClick={()=>clickFunction(e.shape)}
        onMouseEnter={()=>hoverPreview(e.shape)}
      >
        <img src={e.imageUrl}/>
      </div>
    )
  })
  return (
    <div id="shape-list-container">
      <div className="frame">
      </div>
      {shapeDoms}
    </div>
  )
}