import React, { Component } from 'react'
import * as THREE from 'three'

class PreviewScene extends Component {
  constructor(props) {
    super(props)

    this.start = this.start.bind(this)
    this.stop = this.stop.bind(this)
    this.animate = this.animate.bind(this)
  }

  componentDidMount() {
    const width = this.mount.clientWidth
    const height = this.mount.clientHeight

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      1000
    )
    const {geometry} = this.props
    console.log('geometry: ', geometry)
    let geometryType = null
    switch(geometry) {
        case 'pyramid':
            geometryType = new THREE.CylinderGeometry(0, 1.8, 1.8, 4, false)
            break
        case 'box':
            geometryType = new THREE.BoxGeometry(2, 2, 2)
            break
        case 'sphere':
            geometryType = new THREE.SphereGeometry(0.8, 16, 16)
            break
        default:
            geometryType = new THREE.CylinderGeometry(0, 0.8, 2, 4, false)
            break

    }
    const renderer = new THREE.WebGLRenderer({ antialias: true })
    const boxGeometry = new THREE.BoxGeometry(1, 1, 1)
    var sphereGeometry = new THREE.SphereGeometry(0.8, 16, 16)
    var pyramidGeometry = new THREE.CylinderGeometry(0, 0.8, 2, 4, false)
    const material = new THREE.MeshBasicMaterial({ wireframe: true, color: '#00798E' })
    const cube = new THREE.Mesh(geometryType, material)

    camera.position.z = 4
    scene.add(cube)
    renderer.setClearColor('#000000')
    renderer.setSize(width, height)

    this.scene = scene
    this.camera = camera
    this.renderer = renderer
    this.material = material
    this.cube = cube

    this.mount.appendChild(this.renderer.domElement)
    this.start()
  }
  
  componentDidUpdate(){
    // this.stop()
    this.mount.removeChild(this.renderer.domElement)
    const width = this.mount.clientWidth
    const height = this.mount.clientHeight

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      1000
    )
    const {geometry} = this.props
    console.log('geometry: ', geometry)
    let geometryType = null
    switch(geometry) {
        case 'pyramid':
            geometryType = new THREE.CylinderGeometry(0, 1.8, 1.8, 4, false)
            break
        case 'box':
            geometryType = new THREE.BoxGeometry(2, 2, 2)
            break
        case 'sphere':
            geometryType = new THREE.SphereGeometry(1.5, 20, 20)
            break
        default:
            break

    }
    const renderer = new THREE.WebGLRenderer({ antialias: true })
    const boxGeometry = new THREE.BoxGeometry(1, 1, 1)
    var sphereGeometry = new THREE.SphereGeometry(0.8, 16, 16)
    var pyramidGeometry = new THREE.CylinderGeometry(0, 0.8, 2, 4, false)
    const material = new THREE.MeshBasicMaterial({ wireframe: true, color: '#00798E' })
    const cube = new THREE.Mesh(geometryType, material)

    camera.position.z = 4
    scene.add(cube)
    renderer.setClearColor('#000000')
    renderer.setSize(500, 500)

    this.scene = scene
    this.camera = camera
    this.renderer = renderer
    this.material = material
    this.cube = cube

    this.mount.appendChild(this.renderer.domElement)
    this.start()
    console.log('will start')
  }

  componentWillUnmount() {
    this.stop()
    this.mount.removeChild(this.renderer.domElement)
  }

  start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }

  stop() {
    cancelAnimationFrame(this.frameId)
  }

  animate() {
    this.cube.rotation.x += 0.01
    this.cube.rotation.y += 0.01

    this.renderScene()
    this.frameId = window.requestAnimationFrame(this.animate)
  }

  renderScene() {
    this.renderer.render(this.scene, this.camera)
  }

  render() {
    return (
      <div
        className="preview-screen"
        style={{ width: '400px', height: '400px' }}
        ref={(mount) => { this.mount = mount }}
      />
    )
  }
}

export default PreviewScene